import React from "react";

const schedule = [
  {
    time: "1:00 pm – 1:10 pm",
    event: "Introduction",
  },
  {
    time: "1:10 pm – 2:00 pm",
    event: "Invited speaker 1: prof. dr. ir. Luuk Spreeuwers",
    details: "(Data Management and Biometrics group, University of Twente, NL)",
  },
  {
    time: "2:00 pm – 2:15 pm",
    event: "Coffee break",
  },
  {
    time: "2:15 pm – 3:00 pm",
    event: "Invited speaker 2: Prof. dr. ir. Rick Johnson",
    details:
      "(Fellow in Computational Arts and Humanities at the Jacobs Technion-Cornell Institute, USA)",
  },
  {
    time: "3:00 pm – 3:15 pm",
    event: "Coffee break",
  },
  {
    time: "3:15 pm – 4:15 pm",
    event: "Panel session",
  },
  {
    time: "4:15 pm – 5:15 pm",
    event: "Poster session",
  },
  {
    time: "5:15 pm – 5:30 pm",
    event: "Closing remarks",
  },
];

const ScheduleTable = () => {
  return (
    <div className="container my-5">
      <h2 className="text-center mb-4">Schedule</h2>

      <div className="table-responsive d-none d-md-block">
        <table className="table table-striped table-hover align-middle shadow rounded">
          <thead className="table-dark">
            <tr>
              <th scope="col">Time</th>
              <th scope="col">Event</th>
            </tr>
          </thead>
          <tbody>
            {schedule.map((item, index) => (
              <tr key={index}>
                <td className="fw-bold">{item.time}</td>
                <td>
                  {item.event}
                  {item.details && (
                    <>
                      <br />
                      <small className="text-muted">{item.details}</small>
                    </>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="d-block d-md-none">
        {schedule.map((item, index) => (
          <div key={index} className="card mb-3 shadow-sm">
            <div className="card-body">
              <h6 className="card-subtitle mb-2 text-primary fw-bold">
                {item.time}
              </h6>
              <p className="card-text mb-1">{item.event}</p>
              {item.details && (
                <p className="card-text">
                  <small className="text-muted">{item.details}</small>
                </p>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ScheduleTable;
